.about-windows {

    .container {
        @extend %container;
    }

    &__title {
        @include text(50px, 500, 58px, $textColor);
        margin-bottom: 80px;
    }

  
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__left-block {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    &__par {
        @include text(24px, 400, 32px, $textColor);
        width: 800px;
        // margin-bottom: 40px;
    }

    &__item {
        @include text(30px, 500, 35px, $textColor);

        span {
            @include text(24px, 400, 28px, $textColor);
        }
    }

    &__btn {
        width: 247px;
        height: 71px;
        border-radius: 10px;
        background-color: #000000;
        
        @include text(18px, 400, 21px, $buttonTextColor);

        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__more-info {
        @include text(24px, 400, 28px, $textColor);
        border-bottom: 1px solid #000000;
        width: 345px;
        height: 38px;
    }

    &__right-block {
        padding-right: 30px;
    }












    &__item {
        @include text(30px, 500, 35px, $textColor);

        display: flex;
        flex-direction: column;

        width: 645px;
        padding-left: 110px;
        
        position: relative;


    &:first-child {
    
        &::before {
            content: "";
                background-image: url(./../images/window_1.png);
                width: 70px;
                height: 70px;

                position: absolute;
                top: 0%;
                left: 0%;
        }
    }

    &:nth-child(2) {
    
        &::before {
            content: "";
                background-image: url(./../images/window_2.png);
                width: 70px;
                height: 70px;

                position: absolute;
                top: 0%;
                left: 0%;
        }
    }

    &:last-child {
    
        &::before {
            content: "";
                background-image: url(./../images/window_3.png);
                width: 70px;
                height: 70px;

                position: absolute;
                top: 0%;
                left: 0%;
        }
    }
}

}